import React, { Component } from 'react'
import Content from '..//Content/Content'
import './Popular.css'
import { POPULAR_URL, POPULAR } from '../config'
import { handlerLoading } from '../handlerLoading/handlerLoading'
import { getLocalStorage } from '../localStorage/localStorage'
import Loader from '../Loader/Loader'
import ArrowTop from '../ArrowTop/ArrowTop'

class Popular extends Component {
    state = {
        results: [],
        page: 1,
        loading: true
    }

    async componentDidMount() {
        if (getLocalStorage(POPULAR)) {
            const localState = getLocalStorage(POPULAR)
            this.setState({
                results: localState.results,
                page: localState.page,
                loading: false
            })
        } else {
            handlerLoading.call(null, this, POPULAR_URL, POPULAR)
        }
    }

    render() {
        return (
            <>
                <h1 className="display-1 mx-auto">Popular</h1>
                {
                    this.state.loading
                        ? <Loader />
                        : <><div className="container-fluid">
                            <div className="card-deck row justify-content-center my-5">
                                <Content
                                    results={this.state.results}
                                    path="/"
                                />
                            </div>
                        </div>
                        <div>
                            <ArrowTop/>
                            <button
                                className="btn btn-info mx-auto center"
                                onClick={handlerLoading.bind(null, this, POPULAR_URL, POPULAR)}>Load more..</button>
                        </div>
                            
                        </>
                }
            </>
        )
    }
}


export default Popular;